import successImg from "@/assets/ok.png";
export default {
  data() {
    return {
      successImg: successImg,
      num: 3,
      title: this.$route.params.title,
      page: this.$route.params.page,
      RouterUrl: this.$route.params.RouterUrl
    };
  },
  props: {},
  mounted() {
    let timer = setInterval(() => {
      if (this.num <= 1) {
        clearInterval(timer);
        this.$router.push(this.RouterUrl);
      } else {
        this.num--;
      }
    }, 1000);
  }
};



